import React from 'react';
import {Group, CardGrid, Card,
    ModalPage, ModalPageHeader, ModalRoot} from '@vkontakte/vkui';

let Task = ({popoutType, handleClick}) => {

    return (
        <CardGrid onClick={() => handleClick(popoutType)}>
            <Card size='1' mode='shadow' style={{
                width: 'calc(100% - 30px)', border: 'none', borderRadius: '5px',
                padding: '15px', fontSize: '18px', justifyContent: 'center', alignItems: 'center',
                display: 'flex'}}>
                <div>
                 Пожелать добра близкому человеку 
                </div>
            </Card>
        </CardGrid>
    );
}


export default Task;


